import React, { useState, useEffect } from "react";
import logo from "../../assets/images/2gather.jpg";
import AuthController from "../../platform/api/auth";
import {
  isValidEmail,
  isValidPassword
} from "../../platform/services/validator";
import {
  OsTypeEnum,
  LoginProviderTypeEnum
} from "../../platform/constants/enums";
import "./style.scss";

class SignIn extends React.Component {
  state = {
    form: {
      email: "",
      password: ""
    },
    focus: {
      email: false,
      password: false
    },
    passToggle: false
  };

  focusHandler = key => {
    this.setState(prevState => ({
      ...prevState,
      focus: {
        [key]: !prevState.focus[key]
      }
    }));
  };
  // save chi anum :D miat save ara switch ara
  blurHandler = key => {
    this.setState({
      [key]: false
    });
  };

  togglePassword = () => {
    this.setState({
      passToggle: true
    });
  };

  render() {
    const { form, focus, passToggle } = this.state;
    return (
      <div className="sign-in">
        <div className="sign-in-form">
          <div className="sign-in-form-container">
            <h2>Sign in</h2>
            <p>
              Enter your email and password and login in your <br /> account
            </p>
            <form>
              <div className="G-field">
                <label
                  className={`G-form-label  ${
                    focus.email ? "G-label-animated" : ""
                  }`}
                  htmlFor="signin-email"
                >
                  Email
                </label>
                <input
                  type="email"
                  onFocus={() => this.focusHandler("email")}
                  onBlur={() => this.blurHandler("email")}
                />

                <span
                  className={`input-border ${
                    focus.email ? "input-border-animated" : ""
                  }`}
                ></span>
              </div>
              <div className="G-field">
                <label
                  className={`G-form-label  ${
                    focus.password ? "G-label-animated" : ""
                  }`}
                  htmlFor="signin-password"
                >
                  Password
                </label>
                <input
                  type={!passToggle ? "password" : "text"}
                  onFocus={() => this.focusHandler("password")}
                  onBlur={() => this.blurHandler("password")}
                />

                <span
                  className={`input-border ${
                    focus.password ? "input-border-animated" : ""
                  }`}
                ></span>
                <i
                  className={!passToggle ? "icon-ic_eye" : "icon-ic_eyeclosed"}
                  onClick={this.togglePassword}
                ></i>
              </div>

              <button className="G-button">Sign in</button>
            </form>
          </div>
        </div>
        <div className="logo">
          <img src={logo} alt="2gather" />
        </div>
      </div>
    );
  }
}

export default SignIn;
